/**
 * This module contains functions which is related to circumstances when the page is inside an iframe.
 */
'use strict';

/**
 * Adjust the page attributes according to whether the page is inside an iframe.
 */
export function AdjustPageAccordingToWhetherItIsInIframe() {
  if (isInIframe()) {
    const elements = document.body.getElementsByTagName('a');
    for (let i = 0; i < elements.length; i++) {
      elements[i].setAttribute('target', '_blank');
    }
  }
}

/**
 * Copied from: https://stackoverflow.com/a/326076/8581025
 */
function isInIframe() {
  try {
    return window.self !== window.top;
  } catch (e) {
    return true;
  }
}
